import clsx from 'clsx'
import { PropsWithChildren, useEffect } from 'react'

import * as css from './scroll-reveal-container.module.scss'

interface Props extends PropsWithChildren {
  className?: string
}

/**
 * Ïä§ÌÅ¨Î°§Ïù¥ Ìï¥Îãπ Ïó≠ÏòÅÏóê ÏúÑÏπòÌñàÏùÑ Îïå, ÏúÑÎ°ú Ïò¨ÎùºÏò§Î©¥ÏÑú Îì±Ïû•ÌïòÎäî Ìö®Í≥ºÎ•º Ï£ºÎäî Ïª¥Ìè¨ÎÑåÌä∏
 *
 * before: opacity 0, translateY: 50px
 *  -> after: opacity: 1, translateY: 0px
 *
 * @component
 * @param {string} children Ìö®Í≥ºÎ•º Ï§Ñ ÏûêÏãù Ïª¥Ìè¨ÎÑåÌä∏
 * @param {string} className ÏõêÌïòÎäî classÍ∞Ä ÏûàÎã§Î©¥ Ï∂îÍ∞ÄÌï† Ïàò ÏûàÏäµÎãàÎã§.
 *
 * @example
 * <ScrollRevealContainer> // Ìö®Í≥ºÎ•º Ï§Ñ Ïª¥Ìè¨ÎÑåÌä∏Î•º Í∞êÏã∏Ï£ºÎ©¥ Îê©ÎãàÎã§.
 *   <ExampleSection />
 * </ScrollRevealContainer>
 */
export const ScrollRevealContainer = ({ className = '', children }: Props) => {
  useEffect(() => {
    const initObserver = () => {
      const options = {
        root: null,
        threshold: 0.2,
      }

      const observerCallback: IntersectionObserverCallback = (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            entry.target.classList.add(css.visible)
          }
        })
      }

      return new IntersectionObserver(observerCallback, options)
    }

    const myElements = document.querySelectorAll('.animateBox')
    const observer = initObserver()

    myElements.forEach((element) => {
      observer.observe(element)
    })
  }, [children])

  return <div className={clsx('animateBox', css.box, className)}>{children}</div>
}
