import clsx from 'clsx'
import { GatsbyImage, IGatsbyImageData } from 'gatsby-plugin-image'
import { useEffect, useRef } from 'react'

import * as css from './carousel-slider.module.scss'

interface Props {
  logos: IGatsbyImageData[]
  width?: number | string
  reverse?: boolean
}

/**
 * Î°úÍ≥†Îì§ÏùÑ Ï∫êÎü¨ÏÖÄ ÌòïÏãùÏúºÎ°ú ÌëúÏãúÌïòÎäî CarouselSlider Ïª¥Ìè¨ÎÑåÌä∏ÏûÖÎãàÎã§. ÏûêÎèô Î¨¥ÌïúÎ£®ÌîÑ Î∞©ÏãùÏúºÎ°ú ÎèôÏûëÌïòÎ©∞ ÌïÑÏöîÏóê Îî∞Îùº Î∞©Ìñ•ÏùÑ Î∞òÎåÄÎ°ú Ìï† Ïàò ÏûàÏäµÎãàÎã§.
 *
 * @component
 * @param {IGatsbyImageData[]} logos Î°úÍ≥† Ïù¥ÎØ∏ÏßÄ Î∞∞Ïó¥
 * @param {number | string} width Ï∫êÎü¨ÏÉêÏùò Í∞Å ÏïÑÏù¥ÌÖúÏùò ÎÑàÎπÑ (Í∏∞Î≥∏Í∞í: 10%)
 * @param {boolean} reverse trueÏù∏ Í≤ΩÏö∞ Ï∫êÎü¨ÏÉêÏùÄ Î∞òÎåÄ Î∞©Ìñ•ÏúºÎ°ú Ïä¨ÎùºÏù¥ÎìúÎê©ÎãàÎã§. (Í∏∞Î≥∏Í∞í: false)
 *
 * @example
 * <Carousel logos={imageArr} /> // ÏàúÎ∞©Ìñ•
 * <Carousel logos={imageArr} reverse /> // Ïó≠Î∞©Ìñ•
 */
export const CarouselSlider = ({ logos, width = '10%', reverse = false }: Props) => {
  const extendSlideItems = [...logos, ...logos.slice(0, 4)] // ÎÅäÍπÄ ÏóÜÎäî Î£®ÌîÑ Ìö®Í≥ºÎ•º ÏúÑÌï¥ ÏãúÏïºÏóê Î≥¥Ïù¥Îäî Í∞úÏàòÏù∏ 4Í∞úÏùò Ìï≠Î™©ÏùÑ ÎÅùÏóê Ï∂îÍ∞ÄÌï©ÎãàÎã§.
  const carouselRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (carouselRef.current) {
      // Î°úÍ≥† Îç∞Ïù¥ÌÑ∞Ïùò Í∏∏Ïù¥ÏôÄ ÏßÄÏ†ïÎêú ÎÑàÎπÑÏóê Îî∞Îùº ÎèôÏ†ÅÏúºÎ°ú Ï∫êÎü¨ÏÖÄ Ïä§ÌÉÄÏùºÏùÑ Ï°∞Ï†ïÌï©ÎãàÎã§.
      carouselRef.current.style.setProperty('--slide-item-length', `${logos.length}`)
      carouselRef.current.style.setProperty('--slide-item-width', `${width}`)
    }
  }, [logos.length, width])

  return (
    <div ref={carouselRef} className={clsx(css.carousel_container, { [css.carousel_container__reverse]: reverse })}>
      {extendSlideItems.map((logo, idx) => (
        <GatsbyImage
          // eslint-disable-next-line react/no-array-index-key
          key={idx}
          className={css.carousel_item}
          image={logo}
          alt="ÌîÑÎ°úÎçïÌä∏ Î°úÍ≥† Ïù¥ÎØ∏ÏßÄ"
          objectPosition="50% top"
          loading="eager"
        />
      ))}
    </div>
  )
}
